.App {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  font-family: 'Open Sans', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.capture {
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
  margin-bottom: 10px;
  width: 80%;
    right: 0;
    float: right;
}

h5 {
  font-family: 'Passion One', sans-serif;
  font-size: 28px;
  margin: 2px;
}

.date {
  width: 100%;  
  font-weight: bold;
  font-size: 22px;
}

.time {
  float: left;
  margin-top: 250px;
}
.captureLeft {
  width: 20%;
  text-align: right;
  padding-right: 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.clear {
  clear: both;
}

@media only screen and (max-width: 768px){
  .captureLeft { 
    width: 100%;
    padding-right: 0;
    background: #fcfcfc;
    padding: 5px;
    text-align: left;
  }
  .capture {
    float: none;
    width: 100%;
  }
  .capture video {
    max-width: 100%;
  }
  .date {
    position: relative;
  }
  .time {
    float: none;
    margin-top: 0;
  }
}


